import React from "react";
import { useRouteMatch } from "react-router";
import { NavLink } from "react-router-dom";
import styled from "styled-components";

const Nav = ({ route, className }) => {
  const { url } = useRouteMatch();
  return (
    <nav
      className={`${className} user__management__navigation col-12 col-lg-2 d-flex flex-row flex-lg-column`}
    >
      {route.reverse().map(({ path, name }) => (
        <NavLink exact to={`${url}${path}`} activeClassName="active">
          {name}
        </NavLink>
      ))}
    </nav>
  );
};

export default styled(Nav)`
  &.user__management {
    &__navigation {
      margin: 0 70px 0 0;
      a {
        margin: 0 20px 0 0;
        padding: 10px 0;
      }
      .active {
        color: ${(props) => props.theme.colorTextTitle};
        border-bottom: 2px solid ${(props) => props.theme.colorSecondary};
      }
    }
  }
`;
