import React, { lazy, Suspense } from "react";
import { Route, Switch, useRouteMatch } from "react-router";
import styled from "styled-components";
import Nav from "./Nav";
const PdfManagement = lazy(() => import("./PdfManagement"));
const UserInfoManagement = lazy(() => import("./UserInfoManagement"));
const VideoManagement = lazy(() => import("./VideoManagement"));

const UserManagement = ({ className }) => {
  const { path: parentPath } = useRouteMatch();
  const route = [
    { path: "/pdf", component: PdfManagement, name: "File PDF", exact: true },
    { path: "/video", component: VideoManagement, name: "Xem video của bạn", exact: true },
    { path: "", component: UserInfoManagement, name: "Thông tin cá nhân", exact: false },
  ];
  return (
    <main className={`${className} container user__management mt-5 flex-column flex-lg-row`}>
      <Nav route={route} />
      <Suspense
        fallback={
          <div style={{ width: "60%" }} id="loader-wrapper">
            <div id="loader"></div>
          </div>
        }
      >
        <Switch>
          {route.map(({ path, component, name, exact }) => (
            <Route key={name} path={`${parentPath}${path}`} component={component} exact={exact} />
          ))}
        </Switch>
      </Suspense>
    </main>
  );
};

export default styled(UserManagement)`
  display: flex;
  width: 100%;
`;
